<template>
  <div class="plan-edit">
    <el-form :model="newPlanForm" :rules="planFormRule" ref="newPlanForm" label-width="100px" class="gan-form-w" size="small">
      <el-form-item label="计划类型" prop="plan_type">
        <el-select v-model="newPlanForm.plan_type">
          <el-option v-for="(item,index) in typeOption" :key="index" :label="item.label" :value="item.value"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="计划时间" prop="dateRange" class="">
        <el-date-picker v-model="newPlanForm.dateRange" :type="dateConfig.type" class="vm" @change="onRangeChange" :placeholder="'请选择'+dateConfig.holder" value-format="yyyy-MM-dd HH:mm:ss" v-if="newPlanForm.plan_type==1">
        </el-date-picker>
        <el-date-picker v-model="newPlanForm.dateRange" :type="dateConfig.type" class="vm" @change="onRangeChange" :placeholder="'请选择'+dateConfig.holder" format="yyyy 第 WW 周" v-if="newPlanForm.plan_type==2">
        </el-date-picker>
        <el-date-picker v-model="newPlanForm.dateRange" :type="dateConfig.type" class="vm" @change="onRangeChange" :placeholder="'请选择'+dateConfig.holder" value-format="yyyy-MM-dd" v-if="newPlanForm.plan_type==3">
        </el-date-picker>
      </el-form-item>
      <el-form-item label="计划标题" prop="plan_title">
        <el-input v-model="newPlanForm.plan_title"></el-input>
      </el-form-item>
      <el-form-item label="计划内容" prop="plan_content">
        <el-input type="textarea" :rows="4" v-model="newPlanForm.plan_content"></el-input>
      </el-form-item>
      <el-form-item label="上传附件">
        <el-upload class="upload-demo" action="https://jsonplaceholder.typicode.com/posts/" :file-list="fileList" :limit="10" :on-success="onSuccess" :on-remove="handleRemove" :on-preview="handlePreview" multiple>
          <el-button size="small" type="text">点击上传</el-button>
        </el-upload>
      </el-form-item>
      <div class="tc mt10">
        <el-button type="primary" plan @click="save('draft')">保存草稿</el-button>
        <el-button type="primary" @click="save">发布</el-button>
        <el-button @click="onCancel">取消</el-button>
      </div>
    </el-form>
  </div>
</template>

<script>
  import {
    planState,planType
  } from "@/views/const";
  import rule from "@/utils/validate-rule";
  import {
    fmt
  } from "@/utils/util";
  import apis from "@/api";
  import moment from "moment";
  export default {
    data() {
      let validDate = (rule, value, callback) => {;
        if (!value) {
          callback(new Error("请选择计划时间"));
        } else {
          callback();
        }
      };
      return {
        typeOption: planType,
        fileList: [],
        dateConfig: {
          type: "date",
          holder: "日"
        },
        newPlanForm: {
          plan_type: 1,
          start_time: "",
          end_time: "",
          plan_state: "",
          plan_title: "",
          plan_content: "",
          attaches: [],
          dateRange: ""
        },
        planFormRule: {
          dateRange: [
            rule.required({
              message: "计划时间不能为空"
            })
            // {validator:validDate}
          ],
          plan_title: [
            rule.max({
              max: 50,
              message: "长度不能超过50字符"
            }),
            rule.required({
              message: "计划标题不能为空"
            })
          ],
          plan_content: [
            rule.max({
              max: 50,
              message: "长度不能超过1000字符"
            }),
            rule.required({
              message: "计划内容不能为空"
            })
          ]
        },
        planId: '',
      };
    },
    created() {
      this.$watch("newPlanForm.plan_type", function(nv, ov) {
        let vm = this;
        vm.newPlanForm.dateRange = "";
        switch (nv) {
          case 2:
            vm.dateConfig.type = "week";
            vm.dateConfig.holder = "周";
            break;
          case 3:
            vm.dateConfig.type = "month";
            vm.dateConfig.holder = "月";
            vm.dateRange = "";
            break;
          default:
            vm.dateConfig.type = "date";
            vm.dateConfig.holder = "日";
        }
      });
      this.$watch("$route", function(nv, ov) {
        this.planId = this.$route.params.planId
      })
    },
    mounted() {
      this.init();
    },
    computed: {
      id() {
        return this.$route.params.planId;
      }
    },
    watch: {
      fileList: function(nv, ov) {
        this.newPlanForm.attaches = []
        nv.forEach((item, index) => {
          this.newPlanForm.attaches.push({
            attach_name: item.name,
            attach_path: item.url,
            attach_size: item.size
          })
        });
      },
      '$router':function(){
        this.planId = this.$route.params.planId
      }
    },
    methods: {
      init() {
        let vm = this;
        if (vm.id) {
          vm.getPlanDetail();
        }
      },
      async getPlanDetail() {
        debugger
        let result = await apis.plan.getPlanDetail(this.id);
        this.newPlanForm = result;
      },
      save(type) {
        let vm = this;
        this.$refs.newPlanForm.validate(valid => {
          if (valid) {
            debugger
            vm.newPlanForm.plan_state = type == "draft" ? 1 : 2;
            // let params = Object.assign({},vm.newPlanForm)
            // delete params.dateRange
            if (vm.id) {
              apis.plan.postEditPlan(
                  Object.assign({}, vm.newPlanForm, {
                    id: vm.id
                  })
                )
                .then(data => {
                  vm.$alert('编辑保存成功', '提示', {
                    confirmButtonText: "确定",
                    callback: action => {
                      this.$router.push({
                        name: 'planList'
                      })
                    }
                  });
                });
            } else {
              apis.plan.postNewPlan(vm.newPlanForm).then(data => {
                vm.$alert('新建保存成功', '提示',{
                  confirmButtonText: "确定",
                  callback: action => {
                    this.$router.push({
                      name: 'planList'
                    })
                  }
                });
              });
            }
          } else {
            return false;
          }
        });
      },
      onCancel() {
        this.$router.push({
          name: "planList"
        });
      },
      onRangeChange(time) {
        let vm = this
        let startTime = moment(time).subtract(1, 'days').format('YYYY-MM-DD')
        switch (vm.dateConfig.type) {
          case "date":
            vm.newPlanForm.start_time = fmt.getTime(moment(time).format('YYYY-MM-DD'));
            vm.newPlanForm.end_time = fmt.getTime(moment(time).format('YYYY-MM-DD'));
            break;
          case "week":
            let endTime = moment(time).endOf('week').format('YYYY-MM-DD')
            vm.newPlanForm.start_time = fmt.getTime(startTime);
            vm.newPlanForm.end_time = fmt.getTime(endTime);
            break;
          case "month":
            vm.newPlanForm.start_time = fmt.getTime(moment(time).format('YYYY-MM-DD'));
            vm.newPlanForm.end_time = fmt.getTime(moment(time).endOf('month').format('YYYY-MM-DD'));
            break
          default:
            vm.newPlanForm.start_time = '';
            vm.newPlanForm.end_time = '';
        }
      },
      onSuccess(rs, file, fileList) {
        let vm = this
        vm.fileList = fileList
      },
      handleRemove(file, fileList) {
        this.fileList = fileList
      },
      handlePreview(file) {
        console.log(file);
      },
      handleExceed(files, fileList) {
        this.$message.warning(
          `当前限制选择 10 个文件，本次选择了 ${
            files.length
          } 个文件，共选择了 ${files.length + fileList.length} 个文件`
        );
      },
      beforeRemove(file, fileList) {
        return this.$confirm(`确定移除 ${file.name}？`);
      }
    }
  };
</script>


